@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Modak&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Modak&family=Raleway:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,700;1,800;1,900&display=swap);
* {
  box-sizing: border-box;
  font-family: 'Varela Round', sans-serif;
  z-index: 5;
  position: relative;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
  height: 30px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}


p{
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F6F6F6;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

.main-grid {
  display: grid;
  background-color: #F4F4F4;
  height: 100vh;
  grid-template-rows: [row0] 95px [row1] 100px [row2] auto [row3] auto [row4] 120px [row5];
  grid-template-columns: [col0] auto [col1] 200px [col2] 700px [col3] 200px [col4] auto [col5];
  grid-template-areas: "navbar navbar navbar navbar navbar"
                        "margin-left . . . margin-right"
                        "margin-left main-left main main-right margin-right"
                        "margin-left . . . margin-right"
                        "margin-left . . . margin-right";
  /*background: url("images/main-background.jpeg");*/
  background-repeat: no-repeat;
  background-position: right;
  background-size: cover;
  background-position-x: 300px;
}

.top-navbar {
  display: grid;
  grid-template-columns: [col0]40% [col1] 60%[col2];
  grid-area: navbar;
  background-color: #f3f3f3;
  border-bottom: 2px solid #5e5d5d;
  height: 95px;
}
.navbar-menu{
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.navbar-menu-button{
  display: flex;
  width: 140px;
  margin: 5px;
  text-align: center;
  align-items: center;
  justify-content: center;
  transition: 0.1s;
  height: 100%;
}
.navbar-menu-button:hover{
  color: #50bce9;
  cursor: pointer;
}
.navbar-logo{
  color: #50bce9;
  display: flex;
  text-align: center;
  place-content: flex-start;
  place-items: center;
  height: 100%;
}
.navbar-logo > img{
  height: 80px;
}
@media only screen and (max-width: 600px) {
  .main-grid{
    height: 100%;
    grid-template-rows: [row0] 10vh [row1] 10vh [row2] 50vh [row3] 20vh [row4] 10vh [row5];
    grid-template-columns: [col0] 0px [col1] 5vw [col2] 90vw [col3] 5vw [col4] 0px [col5];
  }
  .top-navbar {
    display: grid;
    height: 100%;
    grid-template-columns: [col0]10% [col1] 90%[col2];
  }
  .navbar-menu{
    place-content: center;
  }
  .navbar-menu-button{
    width: 200px;
    font-size: 12px;
    height: 100%;
    font-weight: 600;
  }
  .navbar-logo > h1{
    display: none;
  }
  .navbar-logo > img{
    height: 40px;
  }
}



.welcome-screen {
  display: grid;
  grid-area: main;
  background-color: #ffffff;
  grid-template-rows: 30px 50px 40px 50px 20px 50px 30px 50px auto;
  grid-template-columns: 100px auto 75px 200px 75px auto 100px;
  grid-template-areas:"logo . . . . . ."
                      "welcome-title welcome-title welcome-title welcome-title welcome-title welcome-title welcome-title"
                      ". . . . . . ."
                      ". . welcome-city welcome-city welcome-city . ."
                      ". . . . . . ."
                      ". . welcome-restaurant welcome-restaurant welcome-restaurant . ."
                      ". . . . . . ."
                      ". . . welcome-order . . ."
                      ". . . . . . .";
  border-radius: 20px;
  /*border: 7px solid #f5f5f5;*/
  box-shadow: 0 0 20px rgb(215, 215, 215);
}

.welcome-title {
  text-align-last: center;
  font-family: 'Varela Round', sans-serif;
  grid-area: welcome-title;
  font-size: 25px;
}
.welcome-city, .welcome-restaurant{
  display: flex;
  border-radius: 10px;
  text-align-last: center;
  justify-content: center;
  border: none;
  box-shadow: 0 0 8px rgba(215, 215, 215, 0.72);
  background-color: rgb(255, 255, 255);
  outline: none;
}
.welcome-city:hover, .welcome-restaurant:hover{
  background-color: #fafafa;
  box-shadow: 0 0 15px rgba(215, 215, 215, 0.72);
}
.welcome-city:active{
  border: none;
}
.welcome-city {
  grid-area: welcome-city;
}

.welcome-restaurant {
  grid-area: welcome-restaurant;
}

.welcome-option{
  height: 10px;
  border-radius: 5px;
  background-color: white;
  text-align: center;
  text-align-last: center;
}

.welcome-order {
  font-family: 'Varela Round', sans-serif;
  grid-area: welcome-order;
  border-radius: 30px;
  border: none;
  background-color: rgb(80, 189, 234);
  color: rgb(255, 255, 255);
  letter-spacing: 1px;
  font-size: 15px;
  box-shadow: 0 0 6px #50bce9;
  margin: 0;
}
.welcome-order:hover{
  background-color: rgb(106, 199, 241);
  box-shadow: 0 0 10px #50bce9;
}
.welcome-order:active{
  background-color: rgb(120, 210, 239);
}
.welcome-logo {
  grid-area: logo;
  display: flex;
  position: absolute;
  width: 150px;
  height: 150px;
}
@media only screen and (max-width: 600px) {
  .welcome-screen {
    grid-template-rows: 10% 15% 5% 15% 5% 15% 5% 15%;
    grid-template-columns: 0 5% 15% 60% 15% 5% 0;
    border-radius: 10px;
    box-shadow: 0 0 20px rgb(215, 215, 215);
  }
  .welcome-logo{
    width: 65px;
    height: 65px;
  }
  .welcome-title{
    width: 100%;
    font-size: 14px;
  }
  .welcome-city, .welcome-restaurant{
    width: 100%;
  }
}
.list {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: center;
  align-items: center;
  grid-row-start: row2;
  grid-row-end: row4;
  grid-column-start: col1;
  grid-column-end: col4;
  border-radius: 10px;
  overflow: scroll;
  overflow-x: hidden;
  overflow-y: auto;
}

.category-title {
  grid-row-start: row1;
  grid-row-end: row1;
  grid-column-start: col2;
  grid-column-end: col4;
  white-space: nowrap;
  align-self: center;
  width: 80%;
  height: 50px;
}

.category-title > p {
  font-family: 'Raleway', sans-serif;
  font-weight: 700;
  margin: 0 120px 0 0;
  font-size: 45px;
}

.list-elem {
  display: grid;
  margin: 10px;
  grid-template-columns:[col-photo] 120px [col0]60% [col1] auto [col2];
  grid-template-rows: [row0]40%[row1] 60%[row2];
  height: 120px;
  width: 80%;
  background-color: white;
  border-radius: 20px;
  border: 1px solid #dedede;
  will-change: transform, opacity;
  transition: width 0.2s;
}
.list-elem:hover{
  width: 81%;
}
.list-elem-title {
  align-self: flex-end;
  grid-column-start: col0;
  grid-column-end: col1;
  grid-row-start: row0;
  grid-row-end: row1;
}

.list-elem-title > p {
  font-family: 'Raleway', sans-serif;
  font-weight: 900;
  font-size: 20px;
  padding-top: 20px;
  padding-left: 10px;
}

.list-elem-desc {
  grid-column-start: col0;
  grid-column-end: col1;
  grid-row-start: row1;
  grid-row-end: row2;
  align-self: flex-start;
}

.list-elem-desc > p {
  padding: 10px;
}

.list-elem-button {
  display: flex;
  grid-column-start: col1;
  grid-column-end: col2;
  grid-row-start: row0;
  grid-row-end: row2;
  font-family: 'Varela Round', sans-serif;
  align-items: center;
  justify-content: center;
  align-self: center;
  justify-self: center;
  border-radius: 30px;
  width: 150px;
  height: 40px;
  background-color: rgb(52, 52, 52);
  color: rgb(255, 255, 255);
  letter-spacing: 1px;
  font-size: 15px;
  box-shadow: 2px 3px 6px rgba(2, 10, 14, 0.33);
  margin: 5px;
  transition: 0.1s;
}

.list-elem-button:hover {
  background-color: rgb(19, 19, 19);
  width: 160px;
  cursor: pointer;
}

.list-elem-button:active {
  background-color: rgb(94, 94, 94);
}

.list-elem-photo {
  grid-column-start: col-photo;
  grid-column-end: col0;
  grid-row-start: row0;
  grid-row-end: row2;
  border-radius: 20px 0px 0px 20px;
  box-shadow: white -10px 0px 20px inset;
  object-fit: cover;
  width: 118px;
  height: 118px;
}
.list-elem-price{
  z-index: 100;
  position: absolute;
  color: white;
  display: flex;
  font-family: 'Varela Round', sans-serif;
  font-weight: 900;
  align-items: center;
  justify-content: center;
  margin: 5px;
  background: #282c34;
  border-radius: 50px;
  width: 60px;
  height: 60px;
  top: -10%;
  left: 95%;
  -webkit-transform: rotate(15deg);
          transform: rotate(15deg);
}
.list-elem-cont{
  display: flex;
  justify-self: center;
  align-self: center;

  flex-direction: row;
  justify-content: space-between;

}

.list-back-icon{
  display: flex;
  place-content: center;
  grid-column: col1;
  justify-self: center;
  align-self: center;
  background-color: white;
  width: 50px;
  height: 50px;
  border-radius: 50px;
  box-shadow: 2px 2px 1px gray;
  transition: box-shadow 0.2s , -webkit-transform 0.2s;
  transition: box-shadow 0.2s , transform 0.2s;
  transition: box-shadow 0.2s , transform 0.2s , -webkit-transform 0.2s;
}
.list-back-icon:hover{
  cursor: pointer;
  box-shadow: 2px 2px 1px white;
  -webkit-transform: translate(2px, 2px);
          transform: translate(2px, 2px);
}
.list-back-icon > img{
  width: 32px;
}
@media only screen and (max-width: 600px) {
  .category-title {
    display: flex;
    grid-row-start: row1;
    grid-row-end: row1;
    grid-column-start: col2;
    grid-column-end: col4;
    white-space: nowrap;
    align-self: center;

    place-items: center;
    width: 100%;
    height: 100%;
  }
  .category-title >p {
    font-size: 18px;
  }
  .list-elem {
    grid-template-columns:[col-photo] 25% [col0]45% [col1] 30% [col2];
    grid-template-rows: [row0]30%[row1] 70%[row2];
    border-radius: 8px;
    height: 15vh;
    max-height: 25vw;
    width: 80%;
  }
  .list-elem:hover{
    width: 81%;
  }
  .list-elem-photo {
    width: 100%;
    height: 15vh;
    max-height: 25vw;
    border-radius: 8px 0 0 8px;
  }
  .list-elem-button {
    width: 80%;
    font-size: 9px;
    height: 30%;
    text-align: center;
  }
  .list-elem-button:hover {
    width: 81%;
  }
  .list-elem-price{
    width: 40px;
    height: 40px;
    font-size: 10px;
    top: -10%;
    left: 90%;
  }
  .list-elem-title{
    height: 100%;
    margin-top: 10px;
    white-space: nowrap;
  }
  .list-elem-title > p{
    padding-top: 1.5vh;
    font-size: 15px;
  }
  .list-elem-desc > p {
    padding: 0;
    margin-top: 5px;
    margin-left: 10px;
    font-size: 10px;
  }
  .list-back-icon{
    justify-self: flex-end;
    left: -30px;
    grid-row-start: row1;
    grid-column-start: col2;
    grid-column-end: col4;
    width: 35px;
    height: 35px;
  }
  .list-back-icon > img{
    width: 23px;
  }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.indicator{
    display:flex;
    position:fixed;
    bottom:50px;
    right:50px;
    width:70px;
    height:70px;
    background: white;
    border-radius: 50px;
    -webkit-backdrop-filter:blur(2px);
            backdrop-filter:blur(2px);
    justify-content:center;
    align-items:center;
    box-shadow:0px 0px 10px rgba(200,200,200,0.5);
    transition: 0.1s;
}
.indicator:hover{
    width: 75px;
    height: 75px;
    cursor: pointer;
}
.cartImg{
    display: flex;
    position: relative;
    width: 43px;
    opacity:0.75
}
.itemsCount {
    display:flex;
    position:absolute;
    width:20px;
    height: 20px;
    background: green;
    left:40px;
    top:10px;
    border-radius: 10px;
    text-align:center;
    color:white;
    font-weight:900;
    justify-content: center;
    align-content:center
}
.expanded{
    display:flex;
    flex-direction: column;
    position:fixed;
    bottom:50px;
    right:50px;
    width:400px;
    height:400px;
    background: #F4F4F4;
    border-radius: 15px;
    -webkit-backdrop-filter:blur(9px);
            backdrop-filter:blur(9px);
    justify-content:center;
    align-items:center;
    box-shadow:0px 0px 10px rgba(200,200,200,0.5);
    transition: 0.1s;
    opacity: 1;
}
.expanded-title{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 50px;
}
.expanded-title > p{
    font-family: 'Poppins', sans-serif;
    font-size: 30px;
    font-weight: 600;
    width: 300px;
    padding-left: 20px;
}
.close-button{
    display: flex;
    color: white;
    font-size: 17px;
    font-weight: 900;
    text-shadow: 2px 2px black;
    width: 33px;
    height: 33px;
    margin-left: 50px;
    background-color: #f64545;
    border-radius: 10px;
    place-items: center;
    place-content: center;
    box-shadow: 1px 1px 0 gray;
}
.close-button:hover{
    -webkit-transform: scale(1.02) translate(1px, 1px);
            transform: scale(1.02) translate(1px, 1px);
    cursor: pointer;
    transition:0.1s;
    box-shadow: 1px 1px 0 white;
}
.expanded-list{
    width: 100%;
    height: 300px;
    overflow-y: auto;
}
.expanded-total{
    margin-right: 40px;
    padding: 5px;
    justify-self: right;
    align-self: flex-end;
    font-size: 17px;
}
@media only screen and (max-width: 600px) {
    .indicator{
        bottom:10px;
        right:10px;
        width:70px;
        height:70px;
    }
    .expanded{
        display:flex;
        flex-direction: column;
        position: relative;
        left: 0;
        top: 0;
        width: 100%;
        grid-column-start: col2;
        grid-column-end: col3;
        grid-row-start: row2;
        grid-row-end: row4;
        height: 100%;
    }
    .expanded-title > p{
        width: 85%;
        padding-top: 0px;
        padding-left: 5%;
    }
    .close-button{
        margin: 0;
    }
}
.about {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  max-width: 100%;
  grid-row-start: row1;
  grid-row-end: row5;
  grid-column-start: col0;
  grid-column-end: col5;
  background-color: #f3cce8;
  overflow: hidden;
}

.about-text {
  display: flex;
  /*align-items: center;*/
  width: 600px;
  height: 300px;
  font-family: "Staatliches", sans-serif;
  font-size: 50px;
  letter-spacing: 0.75px;
  margin: 0 40px 0 40px;
  color: black;
}

.about-image {
  width: 500px;
}

.about-image > img {
  width: 100%;
  border-radius: 10px;
}
.about-slide{
  position: absolute;
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
@media only screen and (max-width: 600px) {
  .about-slide{
    flex-direction: column;
  }
  .about-text{
    width: 90%;
    height: 50%;
    font-size: 30px;
    padding: 10px;
  }
  .about-image{
    display: flex;
    place-content: center;
    width: 100%;
  }
  .about-image > img {
    width: 70vw;
    max-height: 40vh;
    border-radius: 10px;
  }
}

.dot-container{
  display: flex;
  flex-direction: row;
  width: 100%;
  place-items: center;
  place-content: center;
  align-self: flex-end;
  padding: 0 0 50px 0;
}
.dot{
  width: 8px;
  height: 8px;
  border-radius: 8px;
  border: 2px solid #282c34;
  margin: 0 10px 0 10px ;
}
.dot:hover{
  cursor: pointer;
}
.button{
  display: flex;
  place-items: center;
  place-content: center;
  border-radius: 50px;
  width: 200px;
  height: 50px;
  margin: 10px;
  box-shadow: 3px 3px 1px #bebebe;
  background: linear-gradient(119deg, rgb(174, 248, 136) 0%, rgb(65, 210, 48) 100%);
  font-family: 'Raleway', sans-serif;
  color: #ffffff;
  text-shadow: 0 0 10px #989696;
  letter-spacing: 0.3px;
  font-weight: 600;
  transition: box-shadow 0.1s, -webkit-transform 0.1s;
  transition: transform 0.1s, box-shadow 0.1s;
  transition: transform 0.1s, box-shadow 0.1s, -webkit-transform 0.1s;
  border: none;
}
.red{
  background: linear-gradient(119deg, rgb(250, 108, 103) 0%, rgb(243, 54, 54) 100%);
}
.button:hover{
  -webkit-transform: translate(-1px, -1px);
          transform: translate(-1px, -1px);
  box-shadow: 4px 4px 2px #bebebe;
  cursor: pointer;
}
.button:active{
  transition: none;
  -webkit-transform: translate(2px, 2px);
          transform: translate(2px, 2px);
  box-shadow: 0px 0px 2px #bebebe;
}
.cart-list-elem{
    display: grid;
    width: 80%;
    height: 50px;
    background-color: white;
    grid-template-columns: 15% auto 100px 60px;
    grid-template-rows: 100%;
    margin: 5px 0 5px 0;
    border-radius: 7px;
    box-shadow: 2px 2px 0 gray;
}
.cart-list-elem-photo{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px 0 0 10px;
}
.cart-list-elem-title{
    display: flex;
    align-items: center;
    padding: 0 0 0 10px;
}
.cart-list-elem-amount{
    font-family: 'Impact', sans-serif;
    display: flex;
    background-color: white;
    flex-direction: row;
    font-size: 30px;
    font-weight: 100;
    align-items: center;
    justify-content: center;
}
.cart-list-elem-amount-button{
    display: flex;
    place-content: center;
    place-items: center;
    border-radius: 10px;
    width: 30px;
    height: 30px;
    color: #555555;
}
.cart-list-elem-amount-button:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
.cart-list-elem-amount-button:active{
    -webkit-transform: scale(1);
            transform: scale(1);
}
.cart-list-elem-amount-button >img{
    width: 100%;
    height: 100%;
}

.cart-list-elem-amount-counter{
    display: flex;
    place-items: center;
    place-content: center;
    background-color: white;
    width: 30px;
    height: 30px;
    font-size: 13px;
    font-weight: normal;
    border: 1px solid #d9d9d9;
    border-radius: 10px;
}
.cart-list-elem-subtotal{
    display: flex;
    place-items: center;
    place-content: center;
    font-size: 13px;
}
@media only screen and (max-width: 600px) {
    .cart-list-elem-title{
        display: flex;
        align-items: center;
        padding: 0 0 0 10px;
        font-size: 13px;
    }
    .cart-list-elem{
        display: grid;
        width: 90%;
        height: 50px;
        background-color: white;
        grid-template-columns: 15% 35% 30% 20%;
        grid-template-rows: 100%;
        margin: 5px 0 5px 0;
